/* schedule.css - Rensa version */

#schedule {
    max-width: 1200px;
    margin: 0 auto;
    padding: 2rem 1rem;
}

.schedule-image {
    width: 100%;
    max-width: 900px; /* Standard max-width fÃ¶r mobil */
    display: block;
    margin: 0 auto 1.5rem;
}
.schedule-description {
    text-align: center;
    color: #fff;
    margin-bottom: 1.5rem;
    font-size: 2rem;
    font-weight: 400;
    font-family: 'Playfair Display', serif;
}

